import React, { forwardRef } from "react";
import { AvatarProps, defaultAvatarProps } from "./Avatar.types";
import {
  AvatarContainer,
  AvatarImage,
  avatarSize,
  AvatarUsername,
} from "./Avatar.styled";
import { getUserInital } from "src/utils";

export const Avatar = forwardRef<HTMLImageElement, AvatarProps>(
  (
    {
      id,
      alt,
      src,
      size,
      radius,
      elevation,
      fit,
      className,
      imageClassName,
      fallbackClassName,
      onClick,
      onLoadingStatusChange,
    },
    ref
  ) => {
    return (
      <AvatarContainer
        className={className}
        css={{
          borderRadius: `$${radius}`,
          boxShadow: `$${elevation}`,
        }}
        size={size}
        onClick={onClick}
      >
        <AvatarImage
          id={id}
          ref={ref}
          src={src}
          alt={alt}
          css={{
            objectFit: fit,
          }}
          className={imageClassName}
          onLoadingStatusChange={onLoadingStatusChange}
        />
        <AvatarUsername
          css={{
            fontSize: avatarSize[size ?? defaultAvatarProps.size!] * 0.7,
          }}
          className={fallbackClassName}
        >
          {getUserInital(alt)}
        </AvatarUsername>
      </AvatarContainer>
    );
  }
);

const componentName = Avatar.displayName || Avatar.name;
Avatar.defaultProps = defaultAvatarProps;
Avatar.displayName = "Avatar" + "(" + componentName + ")";
